.form-order {
    display: flex;
    background-image: url('../images/bigbg.png');
    background-repeat: no-repeat;
    background-size: contain;
    padding-top: 62px;
    padding-bottom: 62px;
    @media (max-width: $bp_desk-m-m) {
        padding-left: 16px;
        padding-right: 16px;
    }
    @media (max-width: $bp-tb-m) {
        width: 100%;
        flex-wrap: wrap;
        background-image: none;
        padding-bottom: 10px;
        padding-top: 25px;
    }
    

    &__contact {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 50%;
        @media (max-width: $bp-tb-m) {
            width: 100%;
            align-items: flex-start;
            margin-bottom: 21px;
        }

        > h2 {
            font-size: 35px;
            font-weight: $normal;
            color: $c_indigo;
            margin-top: 0;
            margin-bottom: 36px;
            @media (max-width: $bp_desk-m-m) {
                text-align: center;
            }
            @media (max-width: $bp-tb-m) {
                font-size: 24px;
                margin-bottom: 16px;
            }
        }

        > span {
            font-size: 16px;
            @media (max-width: $bp-tb-m) {
                text-align: left;
            }
        }

        > p {
            margin-top: 25px;
            margin-bottom: 40px;
            font-size: 16px;
            text-align: center;
            width: 295px;
            @media (max-width: $bp-tb-m) {
                width: 100%;
                margin-bottom: 16px;
                text-align: left;
            }
        }

        > nav {
            display:flex;
            flex-direction: column;
            @media (max-width: $bp-tb-m) {
                text-align: left;
                margin-right: auto;
            }

            > a {
                display: inline-block;
                // width: 100%;
                font-size: 30px;
                color: $c_blue-title;
                &:hover {
                    color: $c_indigo;
                }
            }
        }
        > button {
            font-size: 20px;
            font-weight: $light;
            background-color: $c_indigo;
            color: #fff;
            border-radius: 17px;
            padding: 7px 28px;
            margin: 25px 0 0;
            @media (max-width: $bp-tb-m) {
                width: 100%;
                margin-top: 16px;
            }

            &:hover {
                background-color: $c_indigo-dk;
                box-shadow: 0 0 5px 0 $c_indigo-dk;
            }

        }
    }

    &__box{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 50%;
        @media (max-width: $bp-tb-m) {
            width: 100%;
            align-items: flex-start;
        }

        > h2 {
            font-size: 35px;
            margin-top: 0;
            margin-bottom: 35px;
            font-weight: $normal;
            color: $c_blue-title;
            @media (max-width: $bp-tb-m) {
                font-size: 25px;
                margin-bottom: 14px;
            }
        }
         > label {
            position: relative;
            @media (max-width: $bp-tb-m){
                width: 100%;
            }
        }
        > input, > label  > input {
                font-family: 'Arial';
                border-radius: 20px;
                border: 1px solid #5ba7da;
                font-size: 16px;
                color: $c_blue-border;
                width: 530px;
                margin-bottom: 30px;
                padding: 8px 18px;

                &::placeholder {
                    color: $c_blue-border;
                }
                &:focus {
                    outline: none;
                    box-shadow: 0 0 5px 0 $c_blue-h;
                }

                @media (max-width: $bp_desk-m-m) {
                    width: 100%;
                }
                @media (max-width: $bp-tb-m) {
                    margin-bottom: 16px;
                }

                &.invalid {
                    border-color: $c_red;
                    box-shadow: 0 0 4px 3px $c_red;
                }
            
            
        }
        > textarea {
            font-family: 'Arial';
            padding: 10px;
            width: 530px;
            font-size: 16px;
            color: $c_blue-border;
            border-radius: 20px;
            border: 1px solid $c_blue-border;
            resize: none; 
            @media (max-width: $bp_desk-m-m) {
                width: 100%;
            }
            @media (max-width: $bp-tb-m) {
                margin-bottom: 16px;
            }

            &::placeholder {
                color: $c_blue-border;
            }
            &:focus {
                outline: none;
                box-shadow: 0 0 5px 0 $c_blue-h;
            }
            &.invalid {
                border-color: $c_red;
                box-shadow: 0 0 4px 3px $c_red;
            }
        }
        > button {
            font-size: 20px;
            font-weight: $light;
            background-color: $c_bg_grad;
            color: #fff;
            border-radius: 17px;
            padding: 7px 28px;
            margin: auto 0 0;
            min-width: 204px;
            @media (max-width: $bp-tb-m) {
                width: 100%;

            }

            &:hover {
                background-color: $c_blue-h;
                box-shadow: 0 0 5px 0 $c_blue-h;
                
            }
        }
    }
    &__tel-plc{
        font-family: 'Arial';
        position: absolute;
        font-size: 16px;
        color: $c_blue-border;
        top: 8px;
        left: 16px;
         + .inputMaska {
            padding-left: 86px;
        }
    }

}


.modal-form {
    // display: none;
    height: 0;
    overflow: hidden;


    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 280px;
    z-index: 102;
    background: #fff;
    width: 737px;
    max-width: 100%;
    // padding: 62px 44px 72px;
    max-height: calc(100vh - 90px);
    opacity: 0;
    transition: opacity, top .53s ease;
    
    
    &.open {
        height: auto;
        // overflow-x: hidden;
        // display: block;
        overflow: auto;
        top: 80px;
        opacity: 1;
        padding: 62px 44px 72px;


    }
    .gamburg--wrapper {
        cursor: pointer;
        left: auto;
        right: 16px;
        top: 16px;
    }
    @media (max-width: $bp-tb-m) {
        &.open {
            padding: 30px 10px;
            height: 100%;
            max-height: 100%;
            top: 0;
            left: 0;
            transform: translate(0, 0);
        }
    }
    

    &__box {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        
        > h2 {
            font-size: 35px;
            margin-top: 0;
            margin-bottom: 35px;
            font-weight: $normal;
            @media (max-width: $bp-tb-m) {
                font-size: 25px;
                margin-bottom: 14px;
            }
        }

        > input {
            font-family: 'Arial';
            border-radius: 20px;
            border: 1px solid $c_input_ph;
            font-size: 16px;
            color: $c_input_text;
            width: 525px;
            margin-bottom: 30px;
            padding: 8px 18px;
            max-width: 100%;

            &::placeholder {
                color: $c_input_text;
            }
            &:focus {
                outline: none;
                box-shadow: 0 0 5px 0 $c_svg_silver;
            }

            @media (max-width: $bp_desk-m-m) {
                width: 100%;
            }
            @media (max-width: $bp-tb-m) {
                margin-bottom: 16px;
            }

            &.invalid {
                border-color: $c_red;
                box-shadow: 0 0 4px 3px $c_red;
            }
        }
        > textarea {
            font-family: 'Arial';
            padding: 10px;
            width: 525px;
            font-size: 16px;
            color: $c_input_text;
            border-radius: 20px;
            border: 1px solid $c_input_ph;
            max-width: 100%;
            @media (max-width: $bp_desk-m-m) {
                width: 100%;
            }
            @media (max-width: $bp-tb-m) {
                margin-bottom: 16px;
            }

            &::placeholder {
                color: $c_input_text;
            }
            &:focus {
                outline: none;
                box-shadow: 0 0 5px 0 $c_svg_silver;
            }
            &.invalid {
                border-color: $c_red;
                box-shadow: 0 0 4px 3px $c_red;
            }
        }
        > button {
            font-size: 20px;
            font-weight: $light;
            background-color: $c_indigo;
            color: #fff;
            border-radius: 17px;
            padding: 7px 28px;
            margin: 40px 0 0 0;
            min-width: 204px;
            @media (max-width: $bp-tb-m) {
                width: 100%;

            }

            &:hover {
                background-color: $c_indigo-dk;
                box-shadow: 0 0 5px 0 $c_indigo-dk;
                
            }
            &:active {
                background-color: $c_indigo-act;
                box-shadow: 0 0 12px 0 $c_indigo-act;
                
            }
        }
    }

}


.invalid{
    box-shadow: 0 0 4px 3px $c_red;
    border-color: $c_red;
}

.cart-win__dashboard {
    // display: none;
    height: 0;
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    background-color: black;
    opacity: 0;
    z-index: 100;
    transition: opacity 0.7s ease;
    &.open {
        opacity: 0.7;
        min-height: 200vh;
        height: auto;
        // display: block;
    }
}