.slider-head {
    margin-top: 18px;
    &__box {
        width: 100%;
        margin-bottom: 40px;
    }
    &__item {
        width: 100%;
        display:flex;
    }
    &__content {
        width: 50%;
        padding-top: 80px;
        padding-left: 40px;
        @media (max-width: $bp-tb) {
            padding-top: 10px;
        }
        @media (max-width: $bp-tb-m){
            width: 100%;
        }
        @media (max-width: $bp-tb-m) {
            padding: 16px;
        }
        > form {
            display: flex;
            flex-direction: column;
        }
        h2 {
            font-size: 55px;
            font-weight: $bold;
            color: $c_blue;
            margin: 0 0 25px 0;
            line-height: 1;
            @media (max-width: $bp-tb-m) {
                font-size: 30px;
                margin-bottom: 20px;
            }
        }
        p {
            font-size: 16px;
            color: #555;
            margin-bottom: 40px;
            @media (max-width: $bp-tb-m) {
                margin-bottom: 16px;
            }
        }
        ul {

        }
        li {
            display: inline-block;
            position: relative;
            width: 100%;
            margin-bottom: 15px;
            padding-left: 40px;
            @media (max-width: $bp-tb-m) {
                padding-left: 30px;
            }

            &:after {
                content: '';
                position: absolute;
                left: 7px;
                top: -4px;
                width: 10px;
                height: 20px;
                border-bottom: 4px solid #1d935c;
                border-right: 4px solid rgb(50,192,127);
                transform: rotate(44deg);
            }
        }
        h4 {
            font-size: 20px;
            margin-bottom: 10px;
            font-weight: $bold;
            @media (max-width: $bp-tb-m) {
                margin-top: 0;
            }
        }
        label {
            display: flex;
            position: relative;
            > button {
                background-color: $c_bg_grad;
                padding: 10px 24px;
                border-bottom-right-radius: 20px;
                border-top-right-radius: 20px;
                color: #fff;
                &:hover {
                    background-color: $c_blue-h;
                    font-weight: 500;
                } 
            }
        }
        input {
            font-family: 'Arial';
            width: 396px;
            padding: 12px 18px;
            font-size: 16px;
            border: 1px solid $c_border;
            border-top-left-radius: 20px;
            border-bottom-left-radius: 20px;
            &::placeholder {
                font-family: 'Arial';
                color: $c_input_ph;
            }
            @media (max-width: $bp-tb-m) {
                width: 180px;
            }
            &:focus{
                outline: none;
                box-shadow: 0 0 10px 0 $c_blue;
            }
        }
    }

    &__img{
        width: 50%;
        > img {
            width: 100%;
            margin: auto;
        }
        @media (max-width: $bp_desk-m-m){
            display:flex;
        }
        @media (max-width: $bp-tb-m){
            display:none;
        }
    }
    .flickity-page-dots { 
        bottom: 10px;
        text-align: left;
        left: 30px;
        width: auto;
        @media (max-width: $bp_desk-m-m){
            bottom: -16px;
        }
        @media (max-width: $bp-tb-m) {
            bottom: -3px;
        }
        
        > .is-selected {
            background-color: $c_indigo;
        }
        > .dot {
            margin-right: 0;
        }
    }
    .flickity-button {
        color: $c_silver_dot;
        background-color: transparent;
        
        &:hover {
            color: #212121;
        }
    }
    .flickity-prev-next-button.previous {
        left: 0px;
    }
    .flickity-prev-next-button.next {
        right: 0;
    }
    @media (max-width: $bp-tb-m) {
        .flickity-button {
            display:none;
        }
    }
}
.inputMaska + span {
    display: none;
    position: absolute;
    color: $c_red;
    top: 50px;
    left: 0px;
}
.inputMaska.invalid + span {
    display: inline;
}